import React, { Component } from "react";
import "./tall.css";
import Tshirt from "./tshirt/Tshirt";
import data from "./data.json";
import Filter from "./filter/filter";
import Cart from "./cart/cart";
import Model from "./model/model";
class tall extends Component {
  state = {
    data: data.data,
    size: "",
    sort: "",
    cartitems: JSON.parse(localStorage.getItem("cartitems"))
      ? JSON.parse(localStorage.getItem("cartitems"))
      : [],
    show: false,
  };

  handlesort = (event) => {
    const sort = event.target.value;
    this.setState((state) => ({
      sort: sort,
      data: this.state.data
        .slice()
        .sort((a, b) =>
          sort === "hightolow"
            ? a.price < b.price
              ? 1
              : -1
            : sort === "lowtohigh"
            ? a.price > b.price
              ? 1
              : -1
            : a.id > b.id
            ? 1
            : -1
        ),
    }));
  };

  handlesize = (event) => {
    if (event.target.value === "") {
      this.setState({ size: event.target.value, data: data.data });
    } else {
      this.setState({
        size: event.target.value,
        data: data.data.filter(
          (item) => item.size.indexOf(event.target.value) >= 0
        ),
      });
    }
  };

  handleaddtocart = (item) => {
    console.log(item);
    const cartitems = this.state.cartitems.slice();
    let alreadyInCart = false;
    cartitems.forEach((c) => {
      if (c.id === item.id) {
        c.count++;
        alreadyInCart = true;
      }
    });
    if (!alreadyInCart) {
      cartitems.push({ ...item, count: 1 });
    }
    this.setState({ cartitems });
    localStorage.setItem("cartitems", JSON.stringify(cartitems));
  };

  handleremove = (item) => {
    const cartitems = this.state.cartitems.slice();
    this.setState({ cartitems: cartitems.filter((c) => c.id !== item.id) });
    localStorage.setItem(
      "cartitems",
      JSON.stringify(cartitems.filter((c) => c.id !== item.id))
    );
  };

  handlemodel = (item) => {
    this.setState({ show: true });
    console.log(item);
  };

  handleorder = (order) => {
    alert("your items is" + order.name);
  };
  render() {
    let model = "";
    if (this.state.show === true) {
      model = <Model show={this.state.show} />;
    }
    return (
      <div>
        {model}
        <nav className="navbar navbar-expand-lg  bg-dark">
          <h3>ByForMe </h3>
        </nav>
        <main className="both">
          <div className="main">
            <Filter
              count={this.state.cartitems.length}
              size={this.state.size}
              sort={this.state.sort}
              sortitems={this.handlesort}
              sizeitems={this.handlesize}
            />
            <Tshirt
              data={this.state.data}
              addtocart={this.handleaddtocart}
              model={this.handlemodel}
            />
          </div>
          <div className="cart">
            <Cart
              carditems={this.state.cartitems}
              remove={this.handleremove}
              handleorder={this.handleorder}
            />
          </div>
        </main>
      </div>
    );
  }
}

export default tall;
