import React, { Component } from "react";
import "./model.css";
import T1 from '../image/t1.jpg'
class model extends Component {
  state = {};
  render() {
    return (
      <div className="model">
        <img className="model-img" src={T1} alt="model-img" />
      </div>
    );
  }
}

export default model;
