import React, { Component } from "react";
import "./cart.css";
import T1 from "../image/t1.jpg";
import Fade from "react-reveal/Fade";
class cart extends Component {
  state = {
    name: "",
    email: "",
    address: "",
    showcheckout: false,
  };

  handleshowcheckout = () => {
    this.setState({ showcheckout: true });
  };
  handleinput = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  handleorder = (e) => {
    e.preventDefault();
    const order = {
      name: this.state.name,
      email: this.state.email,
      address: this.state.address,
      carditems: this.props.carditems,
    };
    this.props.handleorder(order);
  };

  render() {
    let checkout = "";
    if (this.state.showcheckout === true) {
      checkout = (
        <Fade right>
          <form className="checkout" onSubmit={this.handleorder}>
            <div>
              <label>Email:</label>
              <input
                name="email"
                type="email"
                required
                onChange={this.handleinput}
              />
            </div>
            <div>
              <label>Name:</label>
              <input
                name="name"
                type="text"
                required
                onChange={this.handleinput}
              />
            </div>
            <div>
              <label>Address:</label>
              <input
                name="address"
                type="text"
                required
                onChange={this.handleinput}
              />
            </div>
            <div>
              <button type="submit" className="btn btn-warning">
                checkout
              </button>
            </div>
          </form>
        </Fade>
      );
    }
    const { carditems } = this.props;
    return (
      <div>
        <div>
          {carditems.length === 0 ? (
            <h6 className="cartitems">Cart is empty...!</h6>
          ) : (
            <h6 className="cartitems">
              You have {carditems.length} item in your cart.
            </h6>
          )}
        </div>
        <div>
          <Fade down cascade>
            <ul className="cart-ul">
              {carditems.map((c) => (
                <li key={c.id}>
                  <diV className="cart-outer-div">
                    <img className="cart-img" src={T1} alt="cart-img" />

                    <div className="card-inner-div">
                      <h6>{c.title}</h6>

                      <div>
                        {c.count} * {c.price}
                        <button
                          onClick={() => this.props.remove(c)}
                          className="remove-btn btn btn-warning"
                        >
                          Remove
                        </button>
                      </div>
                    </div>
                  </diV>
                </li>
              ))}
            </ul>
          </Fade>
        </div>
        <div>
          {carditems.length === 0 ? null : (
            <div className="Proceed">
              Total Price:
              {carditems.reduce((a, b) => a + b.price * b.count, 0)}
              <button
                onClick={this.handleshowcheckout}
                className="Proceed-btn btn btn-primary"
              >
                Proceed
              </button>
            </div>
          )}
        </div>
        <div>{checkout}</div>
      </div>
    );
  }
}

export default cart;
