import React, { Component } from "react";
import "./tshirt.css";
import T1 from "../image/t1.jpg";
import Fade from "react-reveal/Fade";
import Zoom from "react-reveal/Zoom";
import Modal from "react-modal";
class Tshirt extends Component {
  state = {
    item: null,
  };

  openModal = (item) => {
    this.setState({ item });
  };
  closemodal = () => {
    this.setState({ item: null });
  };
  render() {
    const { item } = this.state;
    return (
      <div>
        <Fade bottom cascade={true}>
          <ul className="tshirt-ul">
            {this.props.data.map((item) => (
              <li className="tshirt-li" key={item.id}>
                <div className="card">
                  <a href={"#" + item.id} onClick={() => this.openModal(item)}>
                    <img className="card-img-top" src={T1} alt="tshirt"></img>
                  </a>
                  <div className="card-body center">
                    <h5 className="card-title">{item.title}</h5>
                    <h6 className="card-text">Rs.{item.price}</h6>
                    <button
                      onClick={() => this.props.addtocart(item)}
                      className="btn btn-primary"
                    >
                      Add to Cart
                    </button>
                  </div>
                </div>
              </li>
            ))}
          </ul>
        </Fade>
        {item && (
          <Modal isOpen={true} onRequestClose={this.closemodal}>
            <Zoom>
              <div className="both-modal">
                <div className="first-modal">
                  <img src={T1} alt="modal-img" />
                </div>
                <div className="second-modal">
                  <button className="close-btn" onClick={this.closemodal}>
                    X
                  </button>
                  <h4>{item.title}</h4>
                  <p>{item.descripation}</p>
                  <div>
                    <h6>Avaiable Size</h6>
                    {item.size.map((c) => (
                      <span>
                        <button className="size-btn btn btn-warning">
                          {c}
                        </button>
                      </span>
                    ))}
                  </div>
                  <button
                    onClick={() => {
                      this.props.addtocart(item);
                      this.closemodal();
                    }}
                    className="checkout-btn btn btn-primary"
                  >
                    Add To Cart
                  </button>
                </div>
              </div>
            </Zoom>
          </Modal>
        )}
      </div>
    );
  }
}

export default Tshirt;
